<template>
  <div class="anonymous-noread-list full-screen">
    <div class="page-panel">
      <pem-scroll-view ref="scroll"
        class="scroll-view"
        :data="noreadList"
        :probeType="0"
        :pullup=true
        :pulldown=true
        @pullUpLoad="listMore"
        @pullDownRefresh="listRefresh">
        <div class="scroll-view-container">
          <div class="scroll-view-pulldown">下拉刷新...</div>
          <pem-anonymous-noread-list :list="noreadList"/>
          <div class="scroll-view-pullup">{{isMore ? '加载中...' : '暂时就这么多了...'}}</div>
        </div>
      </pem-scroll-view>
      <pem-operation-panel @backtotop="backtotop" />
      <pem-loading v-show="isLoading"></pem-loading>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import PemAnonymousNoreadList from 'components/pem-anonymous-noread-list/pem-anonymous-noread-list'
import PemOperationPanel from 'components/pem-operation-panel/pem-operation-panel'
import PemScrollView from 'components/scroll-view/scroll-view'
import PemLoading from 'components/pem-loading/pem-loading'
import { MiniSiteApi, ImgPath } from 'api'
import { setTitle } from 'common/js/util'

export default {
  components: {
    PemAnonymousNoreadList,
    PemOperationPanel,
    PemScrollView,
    PemLoading
  },
  data () {
    return {
      projectzj: '',
      currentPage: 1,
      totalPage: 0,
      pageSize: 10,
      noreadList: [],
      hotwordsList: [],
      isMore: true,
      isLoading: true
    }
  },
  created () {
    this._initWXShare()
    this._setTitle('项目工场-未读消息')
    setTimeout(() => {
      this._initData()
    }, 20)
  },
  methods: {
    _initWXShare () {
      this.wxShare({
        title: '为您推荐项目工场',
        content: '我正在使用项目工场，有料的BP、有趣的VC都在这儿，一起来看项目吧！',
        linkUrl: window.location.origin
      })
    },
    _setTitle (title) {
      setTitle(title)
    },
    _initData () {
      this.getAnonymousNoreadList('init')
    },
    backtotop () {
      this.$refs.scroll.scrollTo(0, 0, 300)
    },
    listRefresh () {
      if (!this.isLoading) {
        this.isMore = true
        this.getAnonymousNoreadList('init')
      }
    },
    listMore () {
      if (!this.isLoading && this.isMore) {
        this.getAnonymousNoreadList('more')
      }
    },
    getAnonymousNoreadList (type = 'init') {
      this.isLoading = true
      if (type === 'init') {
        this.currentPage = 1
      }
      this._getAnonymousNoreadList({
        currentPage: +this.currentPage,
        pageSize: +this.pageSize
      }, data => {
        this.isLoading = false
        if (+data.resultcode === 1) {
          this.currentPage = (+data.currentPage || 1) + 1
          if (+data.totalPage <= +data.currentPage) {
            this.isMore = false
          }
          let _list = type === 'init' ? [] : this.noreadList
          _list = _list.concat(data.resultList || []).map(item => {
            item.logo = ImgPath + (item.logo || '').replace(ImgPath, '')
            item.image = (typeof item.image === 'string') ? (ImgPath + item.image.replace(ImgPath, '')) : ''
            return item
          })
          this.noreadList = _list
        }
      })
    },
    _getAnonymousNoreadList (options, callback) {
      MiniSiteApi.getAnonymousNoreadList(options).then(data => {
        callback(data)
      })
    }
  }
}
</script>



